<template>
  <div>
    <h2 id="huanyin">欢迎进入</h2>
    <div class="imgTitle">
      <!--  -->
      <!-- <img :src="indexListNav.zhuyeImg" alt=""> -->
    </div>
    <div class="gonggaoTitle">
      公告
    </div>
    <div class="gonggaoList">
      <div class="gongaoItem" v-for="item in myCurrentData" :key='item.id'>
        <div>
          <h3>{{item.title}}</h3>
          <p>{{item.mytime}}</p>
        </div>
        <p>{{item.content}}</p>
      </div>
    </div>
    <el-pagination center layout="prev, pager, next" :current-page="currentPage" :page-count="myPageCount" @current-change="myhandleCurrentChange">
    </el-pagination>
  </div>
</template>

<script>
import tableMixins from '../mixins/tableMixin'
import { indexListNav } from '../utils/utils'


export default {
    name: '',
    components: {

    },
    mixins: [tableMixins],
    data() {
        return {
            indexListNav,
            noticeData:[],//公告列表
            currentPage: 1,/* 当前页 */
            pageNum: 5,/* 每页显示数据条数 */
        };
    },
    created() {
        this.onSelect({
            url:'/notice/getnotice'
        },(data)=>{
            console.log('公告列表',data);
            this.noticeData = data
        })
    },
    mounted() {

    },
    computed:{
        myCurrentData() {
        // 　　console.log('currentData重新执行',this.myTableData);
            let qishi = (this.currentPage - 1) * this.pageNum
            let jieshu = qishi + this.pageNum
            return this.noticeData.slice(qishi, jieshu)
        },
        /* 总页码  一共多少页 */
        myPageCount() {
            // console.log('重新渲染页数');
            return Math.ceil(this.noticeData.length / this.pageNum)
        }
    },
    methods: {
        myhandleCurrentChange(val) {
            this.currentPage = val
        },
    },
};
</script>

<style scoped lang="less">
.imgTitle {
  width: 100%;
  font-size: 0;
  img {
    width: 100%;
  }
}
#huanyin {
  margin: 1.5rem 0;
}
.gonggaoTitle {
  padding: 2rem;
  margin: 2rem 0 1rem 0;
  background-color: #f3f7f8;
}
.gongaoItem {
  padding: 1rem;
  border-bottom: 1px solid #e7e7e7;
  div {
    display: flex;
    justify-content: space-between;
    margin: 1rem 0;
    color: #393939;
    p {
      color: #9c9c9c;
    }
  }
  p {
    font-style: 14px;
    text-overflow: ellipsis;
    color: #9c9c9c;
  }
}
</style>
